@charset "UTF-8";
body {
  font-family: 微軟正黑體,sans-serif; }

img {
  display: block; }

.banner {
  width: 100%; }
  .banner img {
    width: 100%;
    height: auto; }

.wrap {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%; }
  .wrap .card {
    background-color: aliceblue;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row; }
    .wrap .card .card-photo a img {
      width: 100%;
      height: auto; }
    .wrap .card .card-text {
      text-align: left;
      padding: 20px; }
      .wrap .card .card-text h2 {
        margin-bottom: 30px;
        padding-bottom: 5px;
        border-bottom: 5px solid #fc7f9e; }
      .wrap .card .card-text h2:hover {
        color: aliceblue;
        background-color: #ffa9a9;
        transition: .5s; }
      .wrap .card .card-text p {
        line-height: 1.8; }

* {
  padding: 0;
  margin: 0;
  list-style: none; }
